html,
body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

svg {
    width: 100%;
    height: 100%;// This is explicit for IE to prevent 100vh default
}

img,
svg,
video,
iframe {
    max-width: 100%;
}